const priceAPI = 'https://api.coingecko.com/api/v3/simple/token_price/binance-smart-chain?contract_addresses=0x1583ffe1720027500ba6b284b1b1e9755d43e479&vs_currencies=usd';

const Menu: React.FC = (props) => {
  const { account } = useWeb3React()
  const { login, logout } = useAuth()
  const { selectedLanguage, setSelectedLanguage } = useContext(LanguageContext)
  const { isDark, toggleTheme } = useTheme()
  const priceData = useGetPriceData()
  const cakePriceUsd = priceData ? Number(priceData.data[CAKE.address].price) : undefined
  const profile = useGetLocalProfile()
  const location = useLocation();
  const { onPresentConnectModal } = useWalletModal(login, logout)
  const [eggPrice, setEggPrice] = useState();

  const { children } = props;

  useEffect(() => {

    const intervalId = setInterval(() => {
      getPrice();
    }, 5000)

    return () => {
      clearInterval(intervalId);
    }
  }, [])

  async function getPrice() {
    try {
      const response = await fetch(priceAPI)
      const res = await response.json()
      setEggPrice(res['0x1583ffe1720027500ba6b284b1b1e9755d43e479'].usd.toFixed(9));
    }
    catch(e) {
      console.log('pe 01')
    }
  }

  return (
    <div className="egg-menu-container">
      
      {/* title bar */}
      <div className="title-bar">
        <div className="container content">
          {/* logo */}
          <LogoContainer>
            <Logo src="/images/logo-circle.png" />
            <ValueText>1 EGGPANT = {eggPrice ? `$${eggPrice}` : 'fetching...'}</ValueText>
          </LogoContainer>
          {/* title */}
          <TitleText className="title-text">EGGPLANT DEX</TitleText>
          {/* connect */}
          <ConnectBtnContainer>
            <ConnectBtn onClick={onPresentConnectModal}>
              {account ? 'connected' : 'connect to a wallet'}
            </ConnectBtn>
          </ConnectBtnContainer>
        </div>
      </div>

      {/* menu bar */}
      <div className="menu-bar">
        <div className="container content">
          <Link to="/swap" className={`menu-item ${location.pathname === '/swap' && 'active'}`}>Swap</Link>
          <Link to="/pool" className={`menu-item ${location.pathname === '/pool' && 'active'}`}>Liquidity</Link>
          <div className="menu-item disabled">Pools (coming soon)</div>
        </div>
      </div>
      
      {/* content */}
      <div className="main-content">
        <StarsBG />
        {children}
      </div>
      
      {/* footer */}
      <Footer>
        <FooterContent className="container">
          All rights reserved
        </FooterContent>
      </Footer>

    </div>
  )
}

export default Menu
